import React, { Component } from 'react';
import GoogleLogin from 'react-google-login';

import logo from './JUMP_Bleed.svg';
import './App.css';








const G_FORMACTION = "https://script.google.com/macros/s/AKfycbx5D77_LXUeVqB1vEsHvKH9JPUpL8meAXLmbFPd_ar-CEAE3j0/exec"
//const G_FORMACTION = "https://script.google.com/macros/s/AKfycbzlydGL03jh3jxkNgDuMoDo7hG5c6123TnLxnIJjXuUD4vnL6k/exec"  //psantillan
//const G_FORMACTION = "https://script.google.com/a/ext.uber.com/macros/s/AKfycbzjKfmtAeuzpAIP0zQuBtwgLiPkkx-h1IZXzEuf4Hq8IniGaB8/exec" //psanti3


class App extends Component {
  constructor(props) {
      super(props);
      
      this.state = {
        timestamp: null,
        accessToken: null,
        name: null,
        van: null,
        scooter_qr: null,
        reason: null,
        icon: null,
        notes: "",
        lat: null,
        lon: null,
        scooter_list:[],
        alert_banner:[],
        sheetLoaded: false,
        form_list:[]
      }


      this.addButton = this.addButton.bind(this);
      this.setVan = this.setVan.bind(this);
      this.setName = this.setName.bind(this);
      this.setName = this.setName.bind(this);
      this.setReason = this.setReason.bind(this);
      this.setLatLon = this.setLatLon.bind(this);
      this.setScooter = this.setScooter.bind(this);
      this.setNotes = this.setNotes.bind(this);
      this.getGPS = this.getGPS.bind(this);
      this.setAlert = this.setAlert.bind(this);
      this.validate = this.validate.bind(this);
      this.addNewScooter = this.addNewScooter.bind(this);
      this.removeScooters = this.removeScooters.bind(this);
      this.submitForms = this.submitForms.bind(this);
      this.formSuccess = this.formSuccess.bind(this);
      this.formFail = this.formFail.bind(this);
      this.showQuarantineInfo = this.showQuarantineInfo.bind(this);
      this.clearAlertBanner = this.clearAlertBanner.bind(this);
      this.responseGoogle = this.responseGoogle.bind(this);
      this.vanFocus = this.vanFocus.bind(this);



    }

  componentDidMount(e) {
    this.setState({timestamp: Date.now()})

    if (window.localStorage.getItem("name")){
      this.setState({name : window.localStorage.getItem("name")})
      
      console.log(window.localStorage.getItem("name"))
    } else {
      console.log("no name")
    }

    if (window.localStorage.getItem("van")) {
        this.setState({van : window.localStorage.getItem("van")})
      } else {
        console.log("no van")
    }


    if (window.localStorage.getItem("accessToken")){
      this.setState({accessToken: window.localStorage.getItem("accessToken") })
    }

    this.getGPS()
  }
  
  componentWillUpdate() {
    console.log("FLAGS:")
    for (var f in this.flags) {
      if(this.flags[f]){
        console.log(f + "->" + this.flags[f])
      }
    }
  
  }
  componentDidUpdate(e) {
    console.log("updated", this.state)
    //window.localStorage.clear();
    if (this.state.name){
      window.localStorage.setItem("name", this.state.name);
    }
    if (this.state.van){
      window.localStorage.setItem("van", this.state.van);
    } 

    if (this.state.accessToken){
      window.localStorage.setItem("accessToken", this.state.accessToken);
    } 
    
    console.log("Alerts: ",this.state.alert_banner)
    
    console.log("Scooters: ",this.state.scooter_list)
  
    console.log("state saved", window.localStorage.getItem('name'));
  }


  responseGoogle = (response) => {
        console.log(response);
        if (response){
          this.setState({ gresponse: response, name:response.profileObj.name, accessToken: response.accessToken})
        }
      }

  addButton(label, icon, callback){
    const buttonElem = (<button type="button" name={label} className="btn btn-select btn-lg col-6 col-md-2" onClick={callback}><i className={ icon }></i> <br/> { label } </button>);
    return buttonElem;
  }

  setVan(e){
    var van_number = parseInt(e.target.value);
    if (!(Number.isInteger(van_number))) {
      van_number=null;
    } 

    this.setState({van:van_number});

  }

  setName(e){
    const ft_name = e.target.value;
    this.setState({name:ft_name});

  }

  setReason(e){
    this.getGPS();
    const pickup_reason = e.currentTarget.name;
    console.log(e.currentTarget.name)
    this.setState({reason:pickup_reason, icon:e.currentTarget.children[0].className});
  }

  setScooter(e){
    const time = Date.now()
    console.log("time is ", time);
    const scooter = e.target.value;
    this.setState({ scooter_qr:scooter, timestamp: time });
  }

  setNotes(e){
    const new_note = e.target.value;
    this.setFlagEvent(e)
    this.setState({notes:new_note});
  }

  setLatLon(gps) {
    const curr_lat = gps.coords.latitude;
    const curr_lon = gps.coords.longitude;
    console.log(curr_lon.toPrecision(5));
    this.setState({lat:curr_lat, lon:curr_lon});
    
  }

  setAlert(set_alert) {
    var exists = false;
    console.log(set_alert.key)
    for (var alert in this.state.alert_banner){
      if (set_alert.key ===  this.state.alert_banner[alert].key) {
        exists = true;
      }
    }
    if (!exists) {
      console.log("adding alert");
      this.setState({ alert_banner: [...this.state.alert_banner, set_alert] });
    }

  }

  vanFocus() {
    document.getElementById("van").focus();
  }

  showQuarantineInfo(){
    const alert = <div id="quarantine" key="quarantine" className="alert alert-danger" role="alert" onClick={(e) => this.clearAlertBanner(e)}>
                      <h3>Do Not Attempt Recovery!</h3>
                      <p>Contact JUMP Quarantine Specialist (123) 456-7890</p>
                  </div>

    this.setAlert(alert);

  }

  getGPS() {
    var gps_string="GPS is not available";
    if (navigator.geolocation) {
      gps_string="Loading GPS...";
      navigator.geolocation.getCurrentPosition(this.setLatLon);
    } 

    if ((this.state.lat == null)||(this.state.lon == null)) {
      this.setState({lat:gps_string})
    }

    console.log(gps_string);
  }

  validate(){
    var isValid = false;

    if (((this.state.name == null)||(this.state.name === ""))||(this.state.van == null)){
      const alert = <div key="name_or_van" className="alert alert-warning" role="alert">
                      Check Name / Van #
                      </div>
      console.log("no name or van")
      this.setAlert(alert);
    } else {


    if (this.state.reason){
      if ((this.state.lat == null) || (this.state.lon == null)){

           const alert = <div key = "gps" className="alert alert-warning" role="alert">
                      Waiting For GPS... Please Try Again
                      </div>

          this.setAlert(alert);

       


      } else {
        var time = Date.now()
        console.log("Scooter entry is valid")
        isValid = true;
        this.setState({alert_banner: [], timestamp: time})

      }
    } else {
      const alert = <div className="alert alert-danger" role="alert">
                      Select a Pick Up Reason
                    </div>
      this.setAlert(alert);
    }
  }

    console.log(isValid)
    return isValid;
  }

  addNewScooter() {
    var valid = this.validate()
    if (valid) {
    var formData = new FormData();
    console.log("appending to form")
    formData.append("name", this.state.name);
    formData.append("van", this.state.van);
    formData.append("number", this.state.scooter_qr);
    formData.append("reason", this.state.reason);
    formData.append("notes", this.state.notes);
    formData.append("lat", this.state.lat);
    formData.append("lon", this.state.lon);
    
    this.setState({ form_list: [...this.state.form_list, formData] });

    console.log(formData.values());
    const jsx = <li key={this.state.timestamp} className="list-group-item bg-secondary my-3 clearfix flex-row text-tiny">
                  <button key={this.state.timestamp} id={this.state.timestamp} className="float-left flex-fill btn-lg btn-secondary my-auto" onClick={ e => this.removeScooters(e) } ><i className="fa fa-trash d-none"></i></button>
                  <p className="flex-fill text-white my-auto"> Scooter: {this.state.scooter_qr} | {this.state.reason} <i className={this.state.icon}></i> | {this.state.lat.toPrecision(6)}, {this.state.lon.toPrecision(7)}</p>
                  <span className={(this.state.notes !== "") ? "d-block text-light flex-fill mb-0" : "d-none m-0"}> Notes: {this.state.notes}</span>
                </li>
    this.setState({ scooter_list: [...this.state.scooter_list, jsx], reason: null, icon: null, scooter_qr: null, notes: "", logo: null, lat: null, lon: null })
  }

  }

  removeScooters(e) {
    var new_scooter_list = this.state.scooter_list
    if (e) {
      
      for (var s in this.state.scooter_list) {
        console.log("looking for", e.currentTarget.id, "in: ",this.state.scooter_list[s].key, (e.currentTarget.id === this.state.scooter_list[s].key, s));
        if ((new_scooter_list[s].key === e.currentTarget.id )) {
          console.log("match!", new_scooter_list[s].key, s)
          var pop = new_scooter_list.pop(new_scooter_list[s - 1]);
          console.log("popping", s)
          this.setState({ scooter_list: new_scooter_list })
        }
      }
    } else {
      console.log("no event passed")
      this.setState({scooter_list:[]})

    }
  }

  submitForms(){
    
    
    for (var i = this.state.form_list.length - 1; i >= 0; i--) {
      console.log(this.state.form_list[i].get("name"));
      //fetch(G_FORMACTION, { method: 'POST', headers: new Headers({ authorization: this.state.accessToken), Content-Type: 'application/x-www-form-urlencoded' }), mode: "no-cors", body: this.state.form_list[i]})
      var obj = {  
      method: 'POST',
      headers: {
        'Origin': 'https://d16vo7sc458jxh.cloudfront.net',
        'Access-Control-Allow-Origin': 'https://d16vo7sc458jxh.cloudfront.net',
        'Authorization': 'Bearer ' + this.state.accessToken
    },
        mode: 'no-cors',
        body: this.state.form_list[i]
    }
      fetch(G_FORMACTION, obj)
      .then((e) => this.formSuccess(e))
      //.then(response => console.log('Success!', response))
      .catch(error => this.formFail(error))
      //.catch(error => console.error('Error!', error.message))
      console.log(this.state.form_list[i])
    }
    
    

  }

  formSuccess(e){
    console.log(e);

    if (e.status === 0) {
      const success = <div className="alert alert-success" role="alert">
                      <h3>Success!</h3>
                      </div>

      this.setState({
          scooter_qr: null,
          reason: null,
          icon: null,
          notes: "",
          lat: null,
          lon: null,
          scooter_list:[],
          alert_banner: [success],
          form_list:[]
        })
      } else {
        this.formFail(e);
      }
  }

  formFail(error){
    console.error('Error!', error.message)
    const fail = <div className="alert alert-danger" role="alert">
                      Post database failed! {error.message}
                      </div>

    this.setState({ alert_banner: [...this.state.alert_banner, fail] });
  }

  clearAlertBanner() {
    this.setState({ alert_banner: [] });
  }
  


  render() {
    return (
      <div className="App">
        <img className="App-logo" alt="JUMP" src={logo} />  
        
        <header className="App-header">

          <h1 className="display-4 text-center">Scooter Recovery</h1>
          <div className="d-flex justify-content-end bd-highlight flex-row mt-0">
            
            <GoogleLogin
                  prompt='consent'
                  accessType="offline"
                  clientId="177851411303-58lo72904agk36g0a761601q34ku8953.apps.googleusercontent.com"
                  buttonText="Login"
                  onSuccess={this.responseGoogle}
                  onFailure={this.responseGoogle}
                  
                /> 

            <div className="input-group ml-3">
                <input type="number" pattern="\d*" placeholder={this.state.van ? this.state.van : ""} className="form-control form-control-lg flex-fill" id="van" name="van" min="0" max="9" onChange={(e) => this.setVan(e)} />
                <div className="input-group-append">
                  <button className="btn btn-lg btn-outline-secondary" onClick={() => this.vanFocus()} type="button"><i className="fas fa-shuttle-van p-0 m-0"></i> Van #</button>
                </div>
            </div>

            </div>

            <h4 className="my-3">{this.state.accessToken ? this.state.name : "Please Log In"}</h4>
          
        </header>
        
        <div className="App-body">




          

          <div className="row container-fluid my-auto mx-2">
            
            {this.addButton("Maintenance","fas fa-tools", (e) => this.setReason(e))}
            {this.addButton("Vandalized","fas fa-exclamation-triangle", (e) => this.setReason(e))}
            {this.addButton("Soft Broken","fas fa-cogs", (e) => this.setReason(e))}
            {this.addButton("Stolen/Lost","fas fa-eye-slash", (e) => this.setReason(e))}
            {this.addButton("Recovery","fas fa-plus", (e) => this.setReason(e))}
            {this.addButton("Rebalance","fas fa-sign-out-alt", (e) => this.setReason(e))}
          </div>

          <div className="card text-white bg-dark text-center my-auto mx-3 mb-4">
            <div className="card-header">
            <h3 className="text-light m-0">{this.state.reason ? this.state.reason : "Select Pick Up Reason"} <i className={this.state.icon}></i></h3>  

            </div>
            <div className="card-body">
              <div className="input-group">
                <input type="number" placeholder="Scooter #" className="form-control form-control-lg" onFocus={e => (e.target.value="")} onChange={(e) => this.setScooter(e)} />
                <input type="text" placeholder="Notes" className="form-control form-control-lg" id="notes" onChange={(e) => this.setNotes(e)}></input>
                <div className="input-group-append">
                  <button className="btn btn-lg btn-outline-secondary" onClick={(e) => this.addNewScooter(e)} type="button"><i className="fas fa-plus-circle"></i>  Add</button>
                </div>
              </div>
              <div className="lead my-2">{this.state.alert_banner}</div>
              <ul className="list-group bg-dark text-center mx-auto mt-3">
                {this.state.scooter_list}
              </ul>
              <div className="d-flex justify-content-end bd-highlight">
                <button className={ this.state.alert_banner.length ? "btn btn-outline-secondary text-center p-2 flex-fill" : "d-none"} onClick={ () => this.clearAlertBanner() }><i className="fas fa-success"></i> Clear Alerts</button>
                <div className= {this.state.scooter_list.length ? "flex-row flex-fill my-auto" : "d-none"}>
                  <button className="btn btn-danger m-2 flex-fill" onClick={() => this.removeScooters()}><i className="fas fa-undo"></i> Reset</button>

                  <button className="btn btn-success m-2 flex-fill" onClick={() => this.submitForms()}><i className="fas fa-success"></i> Submit</button>
                </div>
              </div>
              

              
            </div>
            
            <div className="card-footer text-muted text-tiny m-0">
              <div className="d-flex justify-content-center flex-row m-0">
                <div className={this.state.name ? "d-block p-2 flex-fill" : "d-none"}>Field Tech: {this.state.name}</div>
                <div className={(this.state.name && this.state.van) ? "d-block p-2 flex-fill" : "d-none"}>|</div>
                <div className={this.state.van ? "d-block p-2 flex-fill" : "d-none"}>Van: {this.state.van}</div>
                <div className="d-block p-2 flex-fill">|</div>
                <div className={this.state.lat ? "d-block p-2 flex-fill" : "d-none"}>Lat: {this.state.lat}, Lon: {this.state.lon}</div>
                <div className="d-block p-2 flex-fill">|</div>
                <div className="d-block p-2 flex-fill">{new Date().toString()}</div>
              </div>
              
            </div>
          </div>
          <button className="btn btn-lg btn-danger mt-5 mb-1" onClick={() => this.showQuarantineInfo()}><i className="fas fa-radiation p-2"></i> Scooter Quarantine <i className="fas fa-radiation p-2"></i> </button>

        </div>
          
         
          
          
      </div>
    );
  }
}

export default App;
